
*{
  padding: 0;
  margin: 0;
}
.icon{
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
}
.card{
  position: fixed;
  width: 100%;
}
html,body{
  height: 100%;
  overflow-y: hidden;
}
.bg{
  overflow-y: hidden;
  position: relative;
  height: 100vh;
  width: calc(100vh/362*2000);
  background-image: url(/card/static/img/bg.67437e7.svg);
  background-color: #ff3100;
  background-size: cover;
}
.person{
  width:300px;
  transition: .3s linear all;
  position:absolute;
}
.fade-enter-active{
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.down-enter-active{
  transition: transform .5s;
}
.down-enter, .down-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateY(-100vh);
}
